    methods: {
        toexcel() {

            var titleList = [];
            var csvList = [];

            let tableheader = document.querySelectorAll("th");
            tableheader.forEach(function (item) {
                titleList.push(item.innerHTML)
            })
            csvList.push(titleList)
            let tablecontent = document.querySelectorAll("tr")
            // 取得所有資料
            tablecontent.forEach(function (item, index) {
                var regList = [];
                if (index > 0) {
                    item.children.forEach(function (item, index) {
                        if (index >= 0) {
                            regList.push(item.innerHTML)
                        }

                    })
                }
                csvList.push(regList)
            })
            csvList.splice(1, 1)
            // 產生 csv 內容
            let csv;
            csvList.forEach((row) => {
                csv += row.join(',');
                csv += "\n";
            });
            console.log(csvList)
            // 產生 csv 連結
            const anchor = document.createElement('a');
            anchor.href = 'data:text/csv;charset=utf-8,\uFEFF' + encodeURIComponent(csv);
            // anchor.href = csvContent;
            anchor.target = '_blank';
            anchor.download = `${this.currenteventinfo[0]}報名清單.csv`;
            anchor.click();

        },
        clickedtab(index, status) {
            this.currentstatus = status;
            this.currenttabindex = index;
        },
        openmemberinfo(id) {
            this.isopen = true;
            this.clickmemberid = id;
            document.body.scrollTop = document.documentElement.scrollTop = 0;
            this.propsisopen = true;
        },
        closememberifo() {
            this.isopen = false;
            document.body.style.overflow = 'visible'
        },
        search(val) {
            this.keyword = val;
            console.log(val)

        },
        uppage() {
            this.pagenumber--
        },
        nextpage() {
            this.pagenumber++
        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
        convertphone(phone, index) {
            let str = phone;
            if (index == "5") {
                return str = "0" + str
            } else {
                return str
            }

        }

    },
    computed: {
        filtermemberlist: {
            get() {
                return this.listinfo.filter(item => {
                    console.log(item[0])
                    if (this.keyword == "") {
                        return this.listinfo;
                    } else if (item[0].search(this.keyword) != -1) {
                        return item
                    }
                })
            }

        },
        pagecount() {
            let l = this.filtermemberlist.length;
            let s = this.pagesize;
            return Math.floor(l / s)
        },
        paginatedata() {
            const start = this.pagenumber * this.pagesize
            const end = start + this.pagesize;

            return this.filtermemberlist.slice(start, end)
        },
        regisinfo() {
            return this.currenteventinfo[10].split('、')
        },
        userinfo() {
            return this.$store.state.userinfo;
        }
    },
